body{
  display: grid;
  place-items: center;
  min-height: 100vh;
  background: #c5c6d0;
}
.container{
  position: relative;
}

.valentines{
  position: relative;
  top: 50px;
  cursor: pointer;
}
.envelope{
  position: relative;
  width: 300px;
  height: 200px;
  background: #f08080;
  display: grid;
  place-items: center;
}

.envelope::before{
  content: '';
  position: absolute;
  width: 212px;
  height: 212px;
  background: #f08080;
  transform: rotate(45deg);
  top: -105px;
  left: 44px;
  border-radius: 30px 0 0 0;
}

.front{
  position: absolute;
  border-right: 180px solid #f4978e;
  border-top: 90px solid transparent;
  border-bottom: 105px solid transparent;
  left: 120px;
  top: 5px;
  width: 0;
  height: 0;
  z-index: 10;
}

.front::before{
  position: absolute;
  content: '';
  border-left: 300px solid #fda996;
  border-top: 195px solid transparent;
  left: -120px;
  top: -90px;
}

.card{
  position: relative;
  width: 250px;
  height: 180px;
  background: #fff;
  border-radius: 2px;
  display: grid;
  place-items: center;
  transition: 1s;
}

.card::before{
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 2px solid #003049;
  border-style: dashed;
}

.valentines:hover .card{
  transform: translateY(-130px);
  
}
.card .text{
  font-family: 'Brush Script MT', Cursive;
  font-size: 30px;
  line-height: 20px;
}

.text{
  position: absolute;
  top: 45px;
}

.button{
  position: relative;
  top: 28px;
  display: block;
  cursor: pointer;
}

.yes{
  border-radius: 8px;
  margin: 8px;
  font-size: 20px;
  background: rgb(121, 255, 121);
 
}

.no{
  border-radius: 8px;
  margin: 8px;
  font-size: 20px;
  background: rgb(255, 145, 126);
  
}

.yay{
  position: absolute;
  font-family: 'Brush Script MT', Cursive;
  font-size: 50px;
  z-index: 10;
}

.image{
  position: relative;
  left: 118px;
}

.heart {
  position: fixed;
  top: -1vh;
  width: 25px;
  height: auto;
  /* font-size: 40px; */
  transform: translateY(0);
  animation: fall 3s linear forwards;
}

@keyframes fall {
  to {
    transform: translateY(101vh);
  }
}

.num{
  position: relative;
  top: -10px;
}